<template>
  <div>
    <section v-if="!reservarActividad">
      <h2 class="title-section">Actividades</h2>
      <!-- Carousel -->
      <div class="carousel-container">
        <carousel
          :navigationEnabled="true"
          :paginationEnabled="false"
          :perPageCustom="[[375,1],[768, 3], [1024, 3]]"
          class="carousel-slide"
        >
          <slide v-for="(activity, index) in activities" :key="index" class="slide">
            <!--  element in carousel -->
            <img
              class="card-img-top img-fluid d-block mx-auto"
              :src="getImgUrl(activity.image)"
              alt="Card image cap"
            />
            <h3 class="title">{{activity.title}}</h3>
            <div class="card">
              <div class="card-body">
                <div class="card-title">
                  <h5 class="card-title-descr">{{activity.title}}</h5>
                  <h6 class="stars">{{activity.stars}}</h6>
                </div>
                <p class="card-text">{{activity.text1}}</p>
                <div class="mb-4">
                  <button type="button" class="butt butt-md">Mas Info</button>
                </div>
                <div class="mb-2">
                  <button
                    type="button"
                    class="butt butt-lg"
                    @click="checkReservarActividad(index)"
                  >Reservar</button>
                </div>
              </div>
            </div>
          </slide>
        </carousel>
      </div>
    </section>

    <template v-else>
      <section>
        <div class="header-reserve-activity"></div>
        <div class="card">
          <div class="card-body">
            <h5 class="title-section mb-3">{{activities[i].title}}</h5>
            <p class="card-text">{{activities[i].text1}}</p>
            <div v-for="(optionDay,index2) in activities[i].schedule" :key="index2">
                <div v-for="(optionHour,index3) in optionDay.hour" :key="index3"
                  class="schedule" @click="elegirFecha(i,index2, index3);">
                  <div class="day-hour">
                    <div class="day badge badge-pill text-light">{{optionDay.day}}</div>
                    <div class="hour" >
                      {{optionHour}}
                    </div>
                  </div>
                  <div class="cost">
                    <div class="butt butt-price">{{activities[i].price}}</div>
                    </div>
                </div>
            </div>

            <div class="mb-2 mt-5">
              <!-- Button trigger modal -->
              <button
                type="button"
                class="butt butt-lg"
                data-toggle="modal"
                data-target="#modal"
              >Reservar</button>

              <!-- Modal -->
              <!-- <div id="modal">
  <modal :activityR="activities[i].title" :dayR="dayReserved" :hourR="hourReserved"> </modal>
              </div>-->

              <div
                id="modal"
                class="modal fade"
                tabindex="-1"
                role="dialog"
                aria-labelledby="modalLabel"
                aria-hidden="true"
              >
                <div class="modal-dialog" role="document">
                  <div class="modal-content">
                    <div class="modal-header mt-3 mx-auto">
                      <h5 class="modal-title" id="exampleModalLabel">Reserva Confirmada</h5>
                      <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                      </button>
                    </div>
                    <div class="modal-body">
                      <div class="activity">{{activityReserved.title}}</div>
                      <div class>{{dayReserved}} {{hourReserved}}</div>
                    </div>
                    <div class="modal-footer">
                      <button type="button" class="butt butt-lg mx-auto">Invitar</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </template>
  </div>
</template>

<script>
import { Carousel, Slide } from "vue-carousel";
// import Modal from './../components/Modal.vue';
export default {
  name: "activity",
  components: {
    Carousel,
    Slide
    // Modal
  },
  data() {
    return {
      reservarActividad: false,
      confirmarReservarActividad: false,
      activityReserved: "",
      dayReserved: "",
      hourReserved: "",
      priceReserved: "",
      backgColor: false,
      activities: [
        {
          image: "biker-couple-standing-rock-sm.png",
          title: "Rock Cycling",
          stars: 4,
          text1:
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin dapibus libero sit amet quam dictum, id tempor arcu volutpat. Donec nec diam at ante ornare pulvinar. Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
          text2:
            "Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
          price: "$100",
          schedule: [
            {
              day: "10",
              hour: ["9.00", "11.00", "13.00"]
            },
            {
              day: "12",
              hour: ["9.00", "11.00", "13.00"]
            }
          ]
        },
        {
          image: "da2b00168809f33a7d1357e0682d2683.png",
          title: "Climbing",
          stars: 4,
          text1:
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin dapibus libero sit amet quam dictum, id tempor arcu volutpat. Donec nec diam at ante ornare pulvinar. Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
          text2:
            "Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
          price: "$200",
          schedule: [
            {
              day: "10",
              hour: ["9.00", "11.00", "13.00"]
            }
          ]
        },
        {
          image: "fc84bd3e5331c221acba48e08effb3cf.png",
          title: "Yoga",
          stars: 3,
          text1:
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin dapibus libero sit amet quam dictum, id tempor arcu volutpat. Donec nec diam at ante ornare pulvinar. Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
          text2:
            "Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
          price: "$300",
          schedule: [
            {
              day: "10",
              hour: ["9.00", "11.00", "13.00"]
            }
          ]
        }
      ]
    };
  },

  methods: {
    getImgUrl(pic) {
      return require("./../assets/img/" + pic);
    },

    checkReservarActividad(index) {
      this.reservarActividad = !this.reservarActividad;
      this.i = index;
    },

    elegirFecha(index1, index2, index3) {
      let fecha = document.getElementsByClassName('schedule');
      this.activityReserved = this.activities[index1];
      this.dayReserved = this.activityReserved.schedule[index2].day;
      this.hourReserved = this.activityReserved.schedule[index2].hour[index3];
      this.priceReserved = this.activityReserved.price;
       let opcion = this.activityReserved + this.dayReserved + ' '+ this.hourReserved +' '+ this.priceReserved;
       fecha.forEach (function(item) {
            if (item.textContent === opcion) {
              item.style.background = 'rgba(224, 97, 41,.5)';
            }
          })
    },

    checkConfirmarReservarActividad() {
      this.confirmarReservarActividad = !this.confirmarReservarActividad;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../../public/css/styles.scss";
a {
  color: white;
}
.title-section {
  font-size: $f1-8;
  color: $color-secondary;
}
.carousel-slide {
  width: 80%;
  margin: 0em auto;
}
.title {
  font-size: $f1-6;
}
.card {
  margin: 2em auto;
  padding: 1em 2em;
  border-radius: 3em;
  width: 80vw;
  border: none;

.card-title {
  display: flex;
  padding: 1em;
  justify-content: center;
  .card-title-descr {
    font-size: $f1-2;
  }
}
}
.card-text {
  color: $color-primary;
  font-size: $f1;
  padding-bottom: 4em;
}
.schedule {
  box-shadow: 0.2em 0.2em 0.5em rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0.2em 0.2em 0.5em rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0.2em 0.2em 0.5em rgba(0, 0, 0, 0.6);
  -o-box-shadow: 0.2em 0.2em 0.5em rgba(0, 0, 0, 0.6);
  border-radius: 1em;
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  padding: 0.7em;
  margin: 2em 0em;
  .day-hour {
    flex: 40%;
    display: flex;
    flex-flow: column;
    padding-left: 3em;
  
    .day {
      background-color: $color-secondary;
      font-size: $f1-2;
      padding: 0.6em 0.6em;
      margin: 0.1em;
      width: 2.2em;
      height: 2.2em;
    }
     
    .hour {
      font-size: $f1-4;
      display: flex;
      align-items: center;
      margin: 0.1em;
      
      &::before {
        content: '';
        display: inline-block;
        background-image: url(./../assets/img/clock.png);
        background-repeat: no-repeat;
        background-size: 1.58em;
        width: 1.58em;
        height: 1.58em;
        margin-right: 0.5em;
}
    }
  }
  
  .cost {
    flex: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
  }
  &:hover {
    background: rgba($color-secondary, 0.2);
    transition: all 0.3s ease;
  }
}
.header-reserve-activity {
  display: flex;
  background-image: url(./../assets/img/1019.png);
  background-position: center;
  background-size: cover;
  height: 30em;
}

.modal-content {
  padding: 2em;

  .modal-title {
    font-size: $f3;
    color: $color-secondary;
    margin-bottom: 3em;
  }
  .modal-dialog {
    top: 20%;
    left: 10%;
    width: 80%;
  }

.close {
  margin: 0;
  span {
    font-size: 3em;
  }
}
}

.background-selected {
  background-color: "rgba(224, 97, 41,.5)";
}
.background-unselected {
  background-color: white;
}
</style>